::selection {
  background: #963EFF;
  color: #fff; }

a {
  color: #fff;
  text-decoration: underline; }
  a:hover {
    color: #fff;
    text-decoration: none; }

h1, h2, h3, h4, h5, h6 {
  color: #fff;
  font-weight: bold; }

h1 {
  font-size: 38px;
