  border-left: solid 4px #3D3D3D; }

code {
  font-family: 'Roboto Mono', monospace;
  background-color: #282828;
  border-radius: 3px;
  color: #fff;
  padding: 1px 3px; }

.content, footer {
  margin-left: 80px;
  margin-right: 80px; }
  @media (max-width: 424px) {
    .content, footer {
      margin-left: 25px;
      margin-right: 15px; } }
  @media (min-width: 1280px) {
    .content, footer {
      width: 1120px;
      margin-left: auto;
      margin-right: auto; } }

.clear-both {
  clear: both; }

footer {
  display: flex;
  margin-top: 47px;
  margin-bottom: 57px; }
  footer a {
    text-decoration: none; }
  footer .with-love {
    margin-top: 0;
    font-size: 19px; }
    @media (max-width: 424px) {
      footer .with-love {
        margin-top: 25px; }
        footer .with-love .from {
          display: none; } }
    footer .with-love .icon-love {
      margin-left: 3px;
      margin-right: 3px; }
      footer .with-love .icon-love:hover {
        animation: heartbeat 1.2s ease-in-out infinite; }

@keyframes heartbeat {
  0% {
    transform: scale(1); }
  14% {
    transform: scale(1.3); }
  28% {
    transform: scale(1); }
  42% {
    transform: scale(1.3); }
  70% {
    transform: scale(1); } }
  footer .fork-and-fix {
    margin-left: auto;
    font-size: 14px;
    align-self: flex-end; }
    @media (max-width: 424px) {
      footer .fork-and-fix {
        display: none; } }

nav {
  display: flex;
  margin-top: 60px;
  margin-left: 80px;
  margin-right: 80px; }
  @media (max-width: 424px) {
    nav {
      margin-top: 25px;
      margin-left: 25px;
      margin-right: 25px; } }
  @media (min-width: 1280px) {
    nav {
      width: 1120px;
      margin-left: auto;
      margin-right: auto; } }
  nav > a {
    align-self: center;
    color: #747474;
    font-size: 16px;
    line-height: 26px;
    margin-right: 34px;
    text-decoration: none;
    white-space: nowrap;
    transition: color 100ms ease-in-out; }
    @media (max-width: 424px) {
      nav > a {
        margin-right: 10px; } }
    nav > a:hover, nav > a.-active {
      color: #fff; }
    nav > a .icon-github path {
      transition: fill 100ms ease-in-out; }
    nav > a:hover .icon-github path {
      fill: #fff; }
  nav .share {
    box-sizing: border-box;
    color: #fff;
    padding: 9px 34px 11px;
    border: 1px solid #313131;
    transition: border-color 100ms ease-in-out;
    margin-right: 8px; }
    nav .share:hover {
      border-color: #fff; }
    nav .share.-right {
      margin-left: auto; }
    @media (max-width: 640px) {
      nav .share {
        display: none; } }
    @media (max-width: 424px) {
      nav .share {
        display: none; } }
    @media (min-width: 425px) and (max-width: 1023px) {
